<template>
    <div>
        <v-list-item>
            <v-list-item-content>
                <v-list-item-title>: {{ gameInfo.turn }}</v-list-item-title>
                <v-list-item-subtitle>: {{ gameInfo.turn_player }}</v-list-item-subtitle>
                <v-list-item-title>: {{ gameInfo.phase }}</v-list-item-title>
                <v-list-item-subtitle>: {{ gameInfo.phase_player }}</v-list-item-subtitle>
            </v-list-item-content>
        </v-list-item>
        <v-divider class="my-4"></v-divider>
        <v-btn @click="fetchGameInfo" color="primary"></v-btn>
        <v-btn v-if="gameInfo.phase !== 'initialize'" @click="surrender" color="primary"></v-btn>
        <TacticsField :field="gameInfo.field" :selectedUnit="localSelectedUnit" @fetchUnitInfo="fetchUnitInfo"/>
        <v-select v-if="gameInfo.phase == 'initialize'" v-model="localSelectedUnit" :items="builds" item-title="build_name" item-value="build_id" label="" return-object></v-select>
        <v-btn v-if="gameInfo.phase === 'initialize'" @click="completePlacement" color="primary"></v-btn>
        <BuildViewer />
    </div>
</template>

<script setup>
import { defineProps, ref, defineEmits } from 'vue';
import TacticsField from './TacticsField.vue'; // 
import BuildViewer from './BuildViewer.vue'; // 

// Props
const props = defineProps({
    gameInfo: {
        type: Object,
        required: true
    },
    builds: {
        type: Array,
        required: true
    }
});

// Emits
const emit = defineEmits(['completePlacement', 'fetchGameInfo', 'fetchUnitInfo', 'surrender']);

// 
const localSelectedUnit = ref(props.builds[0]); // 

// 
const completePlacement = () => {
    emit('completePlacement');
};

const fetchGameInfo = () => {
    emit('fetchGameInfo');
};

const fetchUnitInfo = (unitId) => {
    emit('fetchUnitInfo', unitId);
};

const surrender = () => {
    emit('surrender');
};
</script>
