<template>
    <div>
        <h3></h3>
        <v-list dense>
            <v-list-item>
                <v-list-item-content>
                    <v-select v-model="localSettings.rule" :items="['normal', 'advanced']" label=""></v-select>
                    <v-select v-model="localSettings.player_order" :items="['random', 'fixed']" label=""></v-select>
                    <v-select v-model="localSettings.initial_area" :items="['normal', 'extended']" label=""></v-select>
                    <v-text-field v-model="localSettings.player_number" label="" type="number" min="2" max="8"></v-text-field>
                    <v-text-field v-model="localSettings.team_number" label="" type="number" min="2" max="4"></v-text-field>
                    <v-text-field v-model="localSettings.max_turn" label="" type="number" min="1" max="100"></v-text-field>
                    <v-text-field v-model="localSettings.field_size" label="" type="number" min="3" max="20"></v-text-field>
                    <v-text-field v-model="localSettings.max_cost" label="" type="number" min="3" max="1000"></v-text-field>
                    <v-text-field v-model="localSettings.max_build" label="" type="number" min="1" max="10"></v-text-field>
                </v-list-item-content>
            </v-list-item>
        </v-list>
        <v-btn @click="updateSettings" color="primary"></v-btn>
        <v-btn @click="agree" color="primary"></v-btn>
        <v-btn @click="leave" color="primary"></v-btn>
    </div>
</template>

<script setup>
import { defineProps, defineEmits, reactive, watch } from 'vue';

const props = defineProps({
    settings: Object
});

const emit = defineEmits(['update', 'agree', 'leave']);

// props.settings
const localSettings = reactive({ ...props.settings });

const updateSettings = () => {
    emit('update', localSettings);
};

const agree = () => {
    emit('agree');
};

const leave = () => {
    emit('leave');
};

// props.settingslocalSettings
watch(() => props.settings, (newSettings) => {
    Object.assign(localSettings, newSettings);
});
</script>
